import { FormBuilder, FormControl, FormControlOptions, FormGroup } from '@angular/forms'
import { Directive } from '@angular/core'

@Directive()
export abstract class Form {
  public form: FormGroup

  public getError(field: string, error: string) {
    return this.form.controls[field]?.errors?.[error]
  }

  public setError(field: string, error: string, message: string|true = true) {
    this.form.controls[field].setErrors({ [error]: message })
  }

  protected _initFormControl(fb: FormBuilder, formFields: object) {
    this.form = fb.group(formFields)
  }

  protected addController(name: string, val: any|null = '', ops: FormControlOptions|null = null) {
    if (! this.form.contains(name)) {
      this.form.addControl(name, new FormControl(val, ops))
    }
  }
}
