import { Component, OnInit } from '@angular/core'
import { ApiService } from '@core/services/api.service'
import { Question } from '@core/models/question.model'
import { Router } from '@angular/router'
import { MicroserviceType } from '@core/enums/microservice-type.enum'

@Component({
  selector: 'app-questions',
  templateUrl: './questions.component.html',
  styleUrls: ['./questions.component.scss']
})
export class QuestionsComponent implements OnInit {

  public questions: Array<Question> = []
  public selected: Array<any> = []
  error: string

  constructor(private api: ApiService, private router: Router) {
  }

  ngOnInit(): void {
    this.api.get('security-questions', MicroserviceType.au)
      .subscribe((data) => {
        this.questions = data
        // this.selected = Array.from(Array(data.length), () => false)
        this.selected = this.questions.map((q) => q.selected)
      })
  }

  evenQuestions(): Array<Question> {
    return this.questions.slice(3).filter((_, i) => !(i % 2))
  }

  oddQuestions(): Array<Question> {
    return this.questions.slice(3).filter((_, i) => i % 2)
  }

  evenIndex(i: number): number {
    return 2 * (i+1) + 1
  }

  oddIndex(i: number): number {
    return 2 * (i+1) + 2
  }

  selectQuestion(question: Question) {
    question.selected = !question.selected
    // this.selected[i] = this.selected[i] !== false ? false : i
    // $event.currentTarget.checked = !$event.currentTarget.checked
    // debugger
  }

  save() {
    const selected = this.questions.filter((q) => q.selected).map(q => q.questionId)
    if (selected.length < 3) {
      this.error = 'Please select at least 3 questions'
      return
    }
    // const selected = this.selected.filter(i => i !== false)

    this.api.post('security-questions', { questions: selected }, MicroserviceType.au)
      .subscribe(() => {
        this.router.navigate(['/'])
      })
  }
}
