import { Component, Input, OnDestroy } from '@angular/core'
import { HttpErrorResponse } from '@angular/common/http'
import { ApiService } from '@core/services/api.service'
import { catchError, finalize, throwError } from 'rxjs'
import { Router } from '@angular/router'
import { Form } from '@core/common/form'
import { FormBuilder, Validators } from '@angular/forms'
import { createPopper } from '@popperjs/core/lib/popper-lite.js'
import { AuthUserService } from '@core/services/auth-user.service'
import {LocalStorageService} from "@core/services/local-storage.service";

@Component({
  selector: 'app-password-login',
  templateUrl: 'password-login.component.html',
  styleUrls: ['password-login.component.scss']
})

export class PasswordLoginComponent extends Form implements OnDestroy {

  public loading = false

  @Input()
  public email: string

  protected formFields = {
    password: ['', Validators.required ]
  }

  private popover: any

  constructor(private api: ApiService, private localStorage: LocalStorageService, private router: Router, private authService: AuthUserService, fb: FormBuilder) {
    super()
    this._initFormControl(fb, this.formFields)
  }

  login() {
    if (this.form.invalid) {
      return
    }

    this.loading = true
    const password = this.form.controls['password'].value
    const data = { email: this.email, password }
    this.api.post('login', data)
      .pipe(
        catchError(({ error: { message } }: HttpErrorResponse) => {
          this.setError('password', 'invalid_password', true)
          return throwError(() => new Error(message))
        })
      )
      .pipe(finalize(() => this.loading = false))
      .subscribe((data) => {
        this.authService.setAuthToken(data.id_token).subscribe()
        // @todo
        const navigateTo = false ? '/auth/questions' : '/account'
        this.router.navigate([navigateTo])
      })
  }

  togglePopover(event: any) {

    event.stopPropagation()

    const reference: HTMLElement = document.querySelector('#reference')!
    const tooltip: HTMLElement = document.querySelector('#tooltip')!

    if (! this.popover) {
      this.popover = createPopper(reference, tooltip, {
        placement: 'bottom',
      })

      document.addEventListener('click', this.hidePopover)
    }

    if (tooltip.hasAttribute('data-show')) {
      tooltip.removeAttribute('data-show')
      return
    }

    const parent = tooltip.parentElement!
    tooltip.style.width = `${parent.offsetWidth}px`
    tooltip.setAttribute('data-show', '')

    // We need to tell Popper to update the tooltip position
    // after we show the tooltip, otherwise it will be incorrect
    this.popover.update()
  }

  hidePopover() {
    const tooltip: HTMLElement = document.querySelector('#tooltip')!
    tooltip.removeAttribute('data-show')
  }

  ngOnDestroy(): void {
    document.removeEventListener('click', this.hidePopover)
  }
}
