import { Component } from '@angular/core'
import { Form } from '@core/common/form'
import { FormBuilder, Validators } from '@angular/forms'
import { ApiService } from '@core/services/api.service'
import { finalize } from 'rxjs'

@Component({
  selector: 'app-forgot-password',
  templateUrl: 'forgot-password.component.html',
  styleUrls: ['forgot-password.component.scss']
})
export class ForgotPasswordComponent extends Form {
  sent: boolean = false
  public loading = false

  private formFields = {
    email: ['', [Validators.required, Validators.email] ]
  }

  constructor(private api: ApiService, fb: FormBuilder) {
    super()
    this._initFormControl(fb, this.formFields)
  }

  reset() {
    if (this.form.valid) {
      this.loading = true
      this.api.post('user/forgot-password', { email: this.form.controls['email'].value })
        .pipe(finalize(() => this.loading = false))
        .subscribe(() => {
          this.sent = true
        })
    }
  }
}
