import { Component, OnInit } from '@angular/core'
import { ApiService } from '@core/services/api.service'
import { FormControl, Validators } from '@angular/forms'
import { catchError, finalize, throwError } from 'rxjs'
import { HttpErrorResponse } from '@angular/common/http'
import { Question } from '@core/models/question.model'
import { MicroserviceType } from '@core/enums/microservice-type.enum'
import { ActivatedRoute } from '@angular/router'

@Component({
  selector: 'app-forgot-email',
  templateUrl: './forgot-email.component.html',
  styleUrls: ['./forgot-email.component.scss']
})
export class ForgotEmailComponent implements OnInit {
  public emailField: FormControl = new FormControl('', [Validators.required, Validators.email])
  public questionVerified = false
  public secretKey: string
  public emailReset = false
  public emailAdded = false

  constructor(private api: ApiService, private route:ActivatedRoute) {}

  ngOnInit() {
    const key = this.route.snapshot.queryParams['key']
    if (key) {
      this.changeEmailWithKey(key)
    }
  }

  public getQuestions() {
    this.emailField.markAsTouched()

    if (this.emailField.valid) {
      this.emailAdded = true
    }
  }

  submit(key: string) {
    this.questionVerified = true
    this.secretKey = key
  }

  private changeEmailWithKey(key: string) {
    this.api.get('reset-email/set-new-email', MicroserviceType.au, { params: { key } })
      .subscribe(() => {
        this.emailReset = true
      })

  }

}
