import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core'
import { FormBuilder } from '@angular/forms'
import { Question } from '@core/models/question.model'
import { Form } from '@core/common/form2'
import { HttpErrorResponse } from '@angular/common/http'
import { catchError, finalize } from 'rxjs'
import { MicroserviceType } from '@core/enums/microservice-type.enum'
import { ApiService } from '@core/services/api.service'

@Component({
  selector: 'app-login-questions',
  templateUrl: 'questions.component.html',
  styleUrls: ['questions.component.scss']
})

export class QuestionsComponent extends Form implements OnInit {

  @Input()
  public email: string

  @Output()
  public submitted = new EventEmitter<string>()

  public questions: Array<Question> = []
  public loading = false

  constructor(fb: FormBuilder, private api: ApiService) {
    super()
    this._initFormControl(fb, {})
  }

  ngOnInit() {
    this.getQuestions()
  }

  submit() {

    if (this.form.invalid) {
      return
    }

    this.questions.forEach((q, i) => {
      this.questions[i].answer = this.getFC(q.questionId).value
    })

    const errorHandler = ({ error }: HttpErrorResponse) => {
      error = JSON.parse(error)
      const errors = error.violations || error.fieldErrors || []
      errors.forEach((x: { field: string, message: string }) => {
        const index = parseInt(x.field.charAt(36))
        this.getFC(this.questions[index].questionId).setErrors({ 'wrong_answer': 'wrong_answer' })
      })

      throw error
    }

    const body = {
      email: this.email,
      answers: this.questions
    }
    this.loading = true
    this.api.post('reset-email/check-answers', body, MicroserviceType.au, { responseType: 'text' })
      .pipe(catchError(errorHandler))
      .pipe(finalize(() => this.loading = false))
      .subscribe((key) => {
        this.submitted.emit(key)
      })
  }

  private getQuestions() {
    this.loading = true

    this.api.get('reset-email', MicroserviceType.au, { params: { email: this.email } })
      .pipe(finalize(() => this.loading = false))
      .subscribe((data) => {
        this.questions = data.answers
      })

  }

}
