import { AfterViewInit, Component, Input, OnInit, ViewChild } from '@angular/core'
import { ApiService } from '@core/services/api.service'
import { Router } from '@angular/router'
import { catchError, finalize, throwError } from 'rxjs'
import { HttpErrorResponse } from '@angular/common/http'
import { AuthUserService } from '@core/services/auth-user.service'
import { MicroserviceType as ms } from '@core/enums/microservice-type.enum'
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog'
import { NgOtpInputComponent } from 'ng-otp-input'
import { OtpInputComponent } from '../../../components/otp-input/otp-input.component'

@Component({
  selector: 'app-code-login',
  templateUrl: './code-login.component.html',
  styleUrls: ['./code-login.component.scss']
})

export class CodeLoginComponent implements AfterViewInit {

  @ViewChild('otpInput') otpInput:OtpInputComponent

  code: string = ''
  trusted: boolean = true
  public invalid: boolean = false
  public accountLocked: boolean
  public loading = false
  public secretKey: string

  @Input()
  public email: string

  constructor(private api: ApiService, private authService: AuthUserService, private router: Router) {
    // this.startRetryInterval()
  }

  ngAfterViewInit() {
    this.otpInput.sendCode()
  }

  login() {
    if (! this.code || this.code.length < 6) {
      return
    }
    this.loading = true
    const data = { secretKey: this.secretKey, code: this.code }
    this.api.post('check-otp-code', data, ms.au)
      .pipe(
        catchError(({ error: { message } }: HttpErrorResponse) => {
          this.invalid = true
          return throwError(() => new Error(message))
        })
      )
      .pipe(finalize(() => this.loading = false))
      .subscribe((data) => {
        // @todo
        this.authService.setAuthToken(data.id_token)
        const navigateTo = false ? '/auth/questions' : '/'
        this.router.navigate([navigateTo])
      })
  }

  onOtpChange($event: any) {
    this.code = $event
  }

  setSecretKey(key: string) {
    this.secretKey = key
    // this.otpSent = !!key
  }
}
