import { NgModule } from '@angular/core'
import { RouterModule, Routes } from '@angular/router'
import { AuthComponent } from './auth.component'
import { LoginComponent } from './pages/login/index/login.component'
import { ForgotEmailComponent } from './pages/forgot-email/index/forgot-email.component'
import { ForgotPasswordComponent } from './pages/forgot-password/forgot-password.component'
import { ResetPasswordComponent } from './pages/reset-password/reset-password.component'
import { QuestionsComponent } from './pages/questions/index/questions.component'
import { AnonymousGuard } from '@core/guards/anonymous.guard'
import { AuthGuard } from '@core/guards/auth.guard'

const routes: Routes = [
  {
    path: '',
    component: AuthComponent,
    children: [
      {
        path: 'login',
        component: LoginComponent,
        canActivate: [AnonymousGuard]
      },
      {
        path: 'questions',
        component: QuestionsComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'forgot/email',
        component: ForgotEmailComponent,
        canActivate: [AnonymousGuard]
      },
      {
        path: 'forgot/password',
        component: ForgotPasswordComponent,
        canActivate: [AnonymousGuard]
      },
      {
        path: 'reset/password/:token',
        component: ResetPasswordComponent,
        canActivate: [AnonymousGuard]
      },
    ]
  }
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthRoutingModule { }
