import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core'
import { HttpErrorResponse } from '@angular/common/http'
import { catchError, throwError } from 'rxjs'
import { MicroserviceType as ms } from '@core/enums/microservice-type.enum'
import { AccountLockedComponent } from '../account-locked/account-locked.component'
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog'
import { ApiService } from '@core/services/api.service'
import { NgOtpInputComponent } from 'ng-otp-input'
import { environment } from 'src/environments/environment'

@Component({
  selector: 'app-otp-input',
  templateUrl: './otp-input.component.html',
  styleUrls: ['./otp-input.component.scss']
})
export class OtpInputComponent {

  @ViewChild(NgOtpInputComponent, { static: false }) ngOtpInput:NgOtpInputComponent

  @Input()
  public email?: string

  @Input()
  public secretKey?: string

  @Input()
  public otpEndpoint?: string

  @Output()
  private inputChange = new EventEmitter<string>()

  @Output()
  private secretKeyAdded = new EventEmitter<string>()

  private retryLimit = 120
  public invalid: boolean = false
  public retryIn: number = 0
  public accountLocked: boolean = false
  public retryIntervalId: ReturnType<typeof setInterval>
  private optSecretKey: string

  constructor(private dialog: MatDialog, private api: ApiService) { }

  onOtpChange($event: string) {
    this.inputChange.emit($event)
  }

  showAccountLockedPopup() {
    this.dialog.open(AccountLockedComponent, {
      width: '428px',
      data: { mins: 2, message: 'Wrong requests too many times.' }
    })
  }

  public sendCode() {

    if (! this.email) {
      return
    }

    if (! this.otpEndpoint) {
      console.error('Endpoint is not set.')
      return
    }

    if (this.accountLocked) {
      this.showAccountLockedPopup()
    }

    const handleError = ({ error }: HttpErrorResponse) => {
      error = JSON.parse(error)
      if (error.detail?.startsWith('Email is restricted for about')) {
        this.handleAccountLocked()
      }
      return throwError(() => new Error(error.detail))
    }

    this.updateSecretKey('')
    this.ngOtpInput.setValue('')

    const data:Record<string, string> = { email: this.email, hostName: environment.lunaServiceUrl }
    if (this.secretKey) {
      data['secretKey'] = this.secretKey
    }

    this.api.post(this.otpEndpoint, data, ms.au, { responseType: 'text' })
      .pipe(catchError(handleError))
      .subscribe((key) => {
        this.updateSecretKey(key)
        this.startRetryInterval()
      })
  }

  updateSecretKey(key: string) {
    this.optSecretKey = key
    this.secretKeyAdded.emit(this.optSecretKey)
  }

  resend(event: MouseEvent) {
    event.preventDefault()

    if (this.retryIn > 0) {
      return
    }

    this.sendCode()
  }

  private startRetryInterval() {
    clearInterval(this.retryIntervalId)
    this.retryIn = this.retryLimit

    this.retryIntervalId = setInterval(() => {
      this.retryIn -= 1
      if (this.retryIn <= 0) {
        clearInterval(this.retryIntervalId)
      }
    }, 1000)
  }

  private startAccountLockedInterval() {
    setTimeout(() => {
      this.accountLocked = false
    }, 1000 * 60 * 2)
  }

  private handleAccountLocked() {
    this.showAccountLockedPopup()
    this.accountLocked = true
    this.startAccountLockedInterval()
    this.ngOtpInput.otpForm.disable()
  }

}
