import { FormBuilder, FormControl, FormControlOptions, FormGroup, Validators } from '@angular/forms'
import { Directive, ElementRef, ViewChild } from '@angular/core'
import { FormValidators } from '@core/utils/form-validators'
import { AutoDestroy } from '@core/common/auto-destroy'
import { finalize } from 'rxjs'

@Directive()
export abstract class Form extends AutoDestroy {
  @ViewChild('saveBtn') saveButton: ElementRef
  @ViewChild('cancelBtn') cancelButton: ElementRef

  public form: FormGroup

  public getError(field: string, error: string) {
    return this.form.controls[field]?.errors?.[error]
  }

  public setError(field: string, error: string, message: string|true = true) {
    this.form.controls[field].setErrors({ [error]: message })
  }

  public getFC(name: string, i: number|null = null, validations: Array<'required'>|null = null) {
    name = this.getFCName(name, i, null, validations)

    return this.form.controls[name]
  }

  protected getFCValue(name: string, i: number|null = null) {
    if (i !== null) {
      name = `${name}-${i}`
    }

    return this.form.controls[name]?.value
  }

  protected _initFormControl(fb: FormBuilder, formFields: object, validators: any = null) {
    let options:object = {}
    if (validators) {
      options = { validators }
      // options['validators'] = validators
    }

    this.form = fb.group(formFields, options)
    // this.form = new FormGroup<any>(formFields, options)
  }

  protected addController(name: string, val: any|null = null, ops: FormControlOptions|null = null) {
    if (! this.form.contains(name)) {
      this.form.addControl(name, new FormControl(val, ops))
    }

    if (val !== null) {
      this.form.controls[name].setValue(val)
    }
  }

  public setFCBYName(name: string, i: number|null, val: any = '', validations: Array<'required'>|null = null) {
    return this.getFCName(name, i, val, validations)
  }

  /**
   * disable form buttons
   */
  protected handleSubmit() {
    const saveBtn = this.saveButton.nativeElement
    const cancelBtn = this.cancelButton?.nativeElement

    if (cancelBtn) {
      cancelBtn.disabled = true
    }

    saveBtn.disabled = true
    saveBtn.dataset['innerHtml'] = saveBtn.innerHTML
    this.saveButton.nativeElement.innerHTML = '<span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>'

    return finalize(() => {
      saveBtn.innerHTML = saveBtn.dataset['innerHtml']
      saveBtn.disabled = false

      if (cancelBtn) {
        cancelBtn.disabled = false
      }

    })
  }

  private getFCName(name: string, i: number|null, val: any = '', validations: Array<'required'>|null) {
    if (i !== null) {
      name = `${name}-${i}`
    }

    const validators: any = []

    validations?.forEach((v) => validators.push(Validators[v]))

    this.addController(name, val, validators as FormControlOptions)

    return name
  }
}
