import { Component } from '@angular/core'
import { Form } from '@core/common/form'
import { FormBuilder, Validators } from '@angular/forms'
import { ApiService } from '@core/services/api.service'
import { ActivatedRoute } from '@angular/router'
import { finalize } from 'rxjs'

@Component({
  selector: 'app-forgot-password',
  templateUrl: 'reset-password.component.html',
  styleUrls: ['reset-password.component.scss']
})
export class ResetPasswordComponent extends Form {
  public done: boolean = false
  public loading = false

  protected formFields = {
    password: ['', Validators.required ],
    newPassword: ['', Validators.required ]
  }

  constructor(private api: ApiService, private route: ActivatedRoute, fb: FormBuilder) {
    super()
    this._initFormControl(fb, this.formFields)
  }

  reset() {
    if (! this.form.valid) {
      return
    }

    const password = this.form.controls['password'].value
    const newPassword = this.form.controls['newPassword'].value

    if (password != newPassword) {
      this.setError('newPassword', 'not_match', true)
      return
    }

    this.loading = true
    const data = { password, newPassword, token: this.route.snapshot.paramMap.get('token') }
    this.api.post('user/reset-password', data)
      .pipe(finalize(() => this.loading = false))
      .subscribe(() => {
        this.done = true
      })
  }
}
