import { Component, Input, ViewChild } from '@angular/core'
import { ApiService } from '@core/services/api.service'
import { catchError, throwError } from 'rxjs'
import { Router } from '@angular/router'
import { Question } from '@core/models/question.model'
import { NgOtpInputComponent } from 'ng-otp-input'
import { OtpInputComponent } from '../../../components/otp-input/otp-input.component'
import { Form } from '@core/common/form2'
import { FormBuilder, Validators } from '@angular/forms'
import { FormValidators } from '@core/utils/form-validators'
import { MicroserviceType } from '@core/enums/microservice-type.enum'

@Component({
  selector: 'app-new-email',
  templateUrl: './new-email.component.html',
  styleUrls: ['./new-email.component.scss']
})

export class NewEmailComponent extends Form {

  @ViewChild('otpInput') otpInput:OtpInputComponent

  @Input()
  public email: string

  @Input()
  public questionSecretKey: string

  public newEmail?: string
  public emailReset: boolean = false
  public invalid: boolean = false
  public otpSent: boolean = false
  public loading = false
  private code: string = ''
  private secretKey: string

  private formFields = {
    email: ['', [Validators.required, Validators.email]],
  }

  constructor(private api: ApiService, private fb: FormBuilder, private router: Router) {
    super()
    this._initFormControl(fb, this.formFields)
  }

  resetEmail() {
    const data = {
      code: this.code,
      secretKey: this.secretKey
    }
    this.api.post('reset-email/check-otp-code', data, MicroserviceType.au)
      .pipe(catchError(() => {
        return throwError(() => new Error('Something bad happened; please try again later.'))
      }))
      .subscribe(() => {
        this.emailReset = true
      })
  }

  onOtpChange($event: any) {
    this.code = $event
  }

  setSecretKey(key: string) {
    this.secretKey = key
    this.otpSent = !!key
  }

  sendOtpCode() {
    if (! this.getFC('email').invalid) {
      this.otpInput.sendCode()
    }
  }
}
