import { Component, OnInit } from '@angular/core'
import { ApiService } from '@core/services/api.service'
import { Form } from '@core/common/form'
import { FormBuilder, Validators } from '@angular/forms'
import { catchError, finalize, throwError } from 'rxjs'
import { HttpErrorResponse } from '@angular/common/http'
import { MicroserviceType } from '@core/enums/microservice-type.enum'
import { LoginType } from '@core/types'
import { ActivatedRoute, Router } from '@angular/router'
import { AuthUserService } from '@core/services/auth-user.service'

@Component({
  selector: 'app-login',
  templateUrl: 'login.component.html',
  styleUrls: ['login.component.scss']
})
export class LoginComponent extends Form implements OnInit {
  public email: string
  public step: number = 1
  public type: LoginType = LoginType.Code
  public loading = false

  protected formFields = {
    email: ['', Validators.required ]
  }

  constructor(fb: FormBuilder, private api: ApiService, private authService: AuthUserService, private route: ActivatedRoute, private router: Router) {
    super()
    this._initFormControl(fb, this.formFields)
  }

  ngOnInit() {
    const key = this.route.snapshot.queryParams['key']
    if (key) {
      this.loginWithKey(key)
    }
  }

  nextStep() {
    if (this.form.invalid) {
      return
    }

    this.email = this.form.controls['email'].value

    const handleError = ({ message }: HttpErrorResponse) => {
      setTimeout(() => this.setError('email', 'not_found', message) )
      return throwError(() => new Error('Something bad happened; please try again later.'))
    }

    this.loading = true
    const options = { params: { email: this.email }, responseType: 'text' }
    this.api.get('user-info/auth-mode', MicroserviceType.usr, options)
      .pipe(catchError(handleError))
      .pipe(finalize(() => this.loading = false))
      .subscribe((data) => {
        this.type = data
        this.step = 2
      })
  }

  private loginWithKey(key: string) {
    this.api.get('login', MicroserviceType.au, { params: { key } })
      .subscribe(( data ) => {
        this.authService.setAuthToken(data.id_token).subscribe()
        this.router.navigate(['/'])
      })
  }
}
